import AsyncStorage from "@react-native-async-storage/async-storage";

class AuthService {
  TOKEN_KEY = 'token';
  INFO_USER = 'info_user'

  getCredentials(): Promise<string | null> {
    return AsyncStorage.getItem(this.TOKEN_KEY);
  }

  setCredentials(token: string): Promise<void> {
    return AsyncStorage.setItem(this.TOKEN_KEY, token);
  }

  setInfoLogin(data: any): Promise<void> {
    return AsyncStorage.setItem(this.INFO_USER, data)
  }

  getInfoLogin(): Promise<any> {
    return AsyncStorage.getItem(this.INFO_USER)
  }

  async removeCredentials(): Promise<void> {
    await AsyncStorage.removeItem(this.TOKEN_KEY)
    return AsyncStorage.removeItem(this.INFO_USER);
  }

  static shared = new AuthService();
}

export default AuthService;