import React, { useCallback, useState } from "react";
import { useAuth } from "../contexts/AuthContext";
import { Alert, Dimensions, ImageBackground, Platform, StyleSheet } from "react-native";
import { Button, Card, Checkbox, Colors, Icon, Image, Text, TextField, Toast, TouchableOpacity, View } from "react-native-ui-lib";
import { image_background, image_eye, image_faceId, image_logo, image_sublogo } from "../../image";
import { colors } from "../../assets/color/color";
import { FontFamily } from "../global";
import { useFocusEffect } from "@react-navigation/native";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { scale, storeFlag, storeUsernameAndPass } from "../contanst";
import { loginApi } from "../api/authApi";
import SplashScreen from "../component/SplashScreen";
import AuthService from "../services/AuthServices";
import ToastComponent from "../component/ToastComponent";
import TouchID from 'react-native-touch-id';
import * as Keychain from 'react-native-keychain';
import { useTranslation, Trans } from 'react-i18next';
import i18next from "../../i18n";

const optionalConfigObject = {
  title: 'Authentication Required', // Android
  imageColor: '#e00606', // Android
  imageErrorColor: '#ff0000', // Android
  sensorDescription: 'Touch sensor', // Android
  sensorErrorDescription: 'Failed', // Android
  cancelText: 'Cancel', // Android
  fallbackLabel: 'Show Passcode', // iOS (if empty, then label is hidden)
  unifiedErrors: false, // use unified error messages (default false)
  passcodeFallback: false, // iOS - allows the device to fall back to using the passcode, if faceid/touch is not available. this does not mean that if touchid/faceid fails the first few times it will revert to passcode, rather that if the former are not enrolled, then it will use the passcode.
};

const Login = ({ navigation }: any) => {
  const { signIn } = useAuth();
  const [email, onChangeEmail] = useState('');
  const [password, onChangePass] = useState('');
  const [secureTextEntry, setSecureTextEntry] = useState(true);
  const [savePassword, setSavePassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [message, setMessage] = useState('')

  useFocusEffect(useCallback(() => {
    (async () => {
      const dataStoreAsync = await AsyncStorage.getItem(storeUsernameAndPass);
      if (dataStoreAsync) {
        const data = JSON.parse(dataStoreAsync)
        onChangeEmail(data.username);
        onChangePass(data.password);
        setSavePassword(data.rememberMe);
      }
    })()
  }, []))

  const handleSubmit = async () => {
    setIsLoading(true)
    const dataBody = {
      "username": email,
      "password": password,
      "rememberMe": savePassword
    }

    console.log(dataBody);

    const response = await loginApi(dataBody)
    if (response && response.access_token) {
      if (dataBody.rememberMe) {
        AsyncStorage.setItem(storeUsernameAndPass, JSON.stringify(dataBody))
      } else {
        AsyncStorage.removeItem(storeUsernameAndPass);
      }
      await AuthService.shared.setCredentials(response.access_token);
      await AuthService.shared.setInfoLogin(JSON.stringify(response.ui));
      signIn()
      setMessage('Đăng nhập thành công')
      navigation.navigate('AccountScreen', { username: email, password: password })
      // await AsyncStorage.setItem(storeFlag, 'true');
    } else {
      setMessage('Đăng nhập không thành công')
      // navigation.navigate('AccountScreen')
    }
    setIsLoading(false);
    setShowToast(true);
  }

  const handleLoginWithTouchId = async () => {
    TouchID.isSupported(optionalConfigObject)
      .then(biometryType => {
        if (biometryType === 'FaceID') {
          console.log('FaceID is supported.');
        } else {
          console.log('TouchID is supported.');
        }
      })
      .catch(error => {
        console.log(error);
      });

    try {
      const dataStoreAsync = await AsyncStorage.getItem(storeUsernameAndPass);
      const checkStoreFlag = await AsyncStorage.getItem(storeFlag);

      if (checkStoreFlag === 'true') {
        if (dataStoreAsync) {
          TouchID.authenticate('KiTa', optionalConfigObject)
            .then(async (success: any) => {
              console.log('Authentication success:', success);
              // navigation.navigate('AccountScreen');

              const data = JSON.parse(dataStoreAsync);
              const accBody = {
                "username": data.email,
                "password": data.password,
              }
              const response = await loginApi(accBody)
              await AuthService.shared.setCredentials(response.access_token);
              await AuthService.shared.setInfoLogin(JSON.stringify(response.ui));
            })
            .catch(async (error: any) => {
              console.log('Authentication error:', error);
              Alert.alert('Authentication Failed');
            });
        } else {
          Alert.alert(i18next.t('loginScr.notiFaceID'));
        }
      } else {
        Alert.alert(i18next.t('loginScr.notiFaceID'));
      }
    } catch (error) {
      console.log('Error accessing AsyncStorage:', error);
      Alert.alert('Error accessing stored data');
    }

  }

  return (
    <ImageBackground
      resizeMode="cover"
      source={image_background}
      style={styles.imageBackground}
    >
      <View style={{ flex: 1 }}>
        <Image style={styles.logo} source={image_logo} />
      </View>
      <View style={{ flex: 0.2 }}>
        <Text style={styles.headerText} center>{i18next.t('loginScr.title')}</Text>
      </View>
      <View style={{ flex: 1.2, alignItems: "center", justifyContent: "flex-start" }}>
        <TextField
          placeholder={i18next.t('loginScr.acc')}
          floatingPlaceholder
          onChangeText={onChangeEmail}
          enableErrors
          fieldStyle={{ ...styles.containerTextField }}
          value={email}
          validationMessagePosition="bottom"
          validateOnChange
          validate={['required', 'email']}
          validationMessage={['Field is required', 'Email is invalid']}
          validationMessageStyle={{ marginTop: 5, fontFamily: "Inter", fontSize: scale(12) }}
          maxLength={30}
        />

        <TextField
          secureTextEntry={secureTextEntry}
          placeholder={i18next.t('loginScr.password')}
          floatingPlaceholder
          onChangeText={onChangePass}
          fieldStyle={{ ...styles.containerTextField }}
          enableErrors
          validationMessagePosition="bottom"
          validate={['required']}
          validationMessage={['Field is required']}
          validateOnChange
          value={password}
          maxLength={30}
          validationMessageStyle={{ marginTop: 5, fontFamily: "Inter", fontSize: scale(12) }}
          trailingAccessory={<TouchableOpacity onPress={() => setSecureTextEntry(!secureTextEntry)}>
            <Icon style={{ width: scale(20), height: scale(13), }} source={image_eye} />
          </TouchableOpacity>}
        />

        <View style={{ flexDirection: "row", justifyContent: "space-between", alignItems: "center", columnGap: width * 0.3 }}>
          <TouchableOpacity onPress={() => setSavePassword(!savePassword)} style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 8 }}>
            <Checkbox containerStyle={{ borderColor: 'rgba(0, 53, 128, 0.35)' }} borderRadius={8} size={16} value={savePassword} onValueChange={() => setSavePassword(!savePassword)} />
            <Text style={{ color: savePassword ? 'rgba(0, 53, 128, 1)' : 'rgba(0, 53, 128, 0.35)', fontWeight: "400", fontSize: 12, fontFamily: FontFamily.interMedium }}>{i18next.t('loginScr.remember')}</Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => navigation.navigate('ForgetPassword')}>
            <Text style={{ color: colors.primary, fontWeight: "400", fontSize: scale(12), fontFamily: FontFamily.interMedium }}>{i18next.t('loginScr.forgotPassword')}</Text>
          </TouchableOpacity>
        </View>
        <View style={{ width: "85%", alignItems: "center", justifyContent: "space-between", flexDirection: "row" }}>
          <Button onPress={handleSubmit} labelStyle={{ fontWeight: "500", fontSize: scale(14), color: "#fff", fontFamily: FontFamily.interMedium, }} style={{ height: 40, width: "85%", borderRadius: 8, marginTop: 24 }} label={i18next.t('loginScr.title')} size={Button.sizes.large} backgroundColor={colors.primary} />
          <TouchableOpacity onPress={handleLoginWithTouchId}>
            <Image style={{ width: 40, height: 40, marginTop: 24 }} source={image_faceId} />
          </TouchableOpacity>
        </View>
      </View>
      <View style={{ flex: 0.4, alignItems: "center", justifyContent: "center" }}>
        <Image style={{ width: 83, height: 52.52 }} source={image_sublogo} />
      </View>
      {isLoading && <SplashScreen />}
      {showToast && <ToastComponent open={showToast} onOpen={setShowToast} message={message} />}
    </ImageBackground>
  )
}

const { width, height } = Dimensions.get("screen")

const styles = StyleSheet.create({
  imageBackground: {
    flex: 1
  },
  containerTextField: {
    borderWidth: 1,
    borderStyle: "solid",
    borderRadius: 8,
    width: "85%",
    paddingHorizontal: Platform.OS === 'ios' ? 16 : 10,
    borderColor: 'rgba(0, 53, 128, 0.15)',
    paddingVertical: scale(18)
  },
  logo: {
    width: 278,
    height: 52,
    top: height * 0.18,
    left: width * 0.1
  },
  headerText: {
    color: colors.primary,
    fontWeight: "600",
    fontSize: scale(24),
    fontFamily: FontFamily.interMedium
  }
})

export default Login;

