import { createNativeStackNavigator } from "@react-navigation/native-stack";
import React from "react";
import SignUp from "../screen/SignUpScreen";
// import LoginNavigation from "./LoginNavigation";
// import ForgetPasswordNavigation from "./ForgetNavigation";
import Login from "../screen/LoginScreen";
import ForgetPassword from "../screen/ForgetPassword";
import AccountScreen from "../screen/AccountScreen"

export type MainStackParamsList = {
  SignIn: undefined,
  SignUp: undefined,
  ForgetPassword: undefined,
  AccountScreen: undefined
}

const Main = createNativeStackNavigator<MainStackParamsList>();

const MainNavigation = () => {
  return (
    <Main.Navigator
      screenOptions={{
        headerShown: false
      }}
      initialRouteName="SignIn"
    >
      <Main.Screen name="SignIn" component={Login} />
      <Main.Screen name="SignUp" component={SignUp} />
      <Main.Screen name="AccountScreen" component={AccountScreen} />
      <Main.Screen name="ForgetPassword" component={ForgetPassword} />
    </Main.Navigator>
  )
}

export default MainNavigation;