import React, { useState } from "react";
import { Dimensions, Platform, SafeAreaView, StyleSheet } from "react-native";
import { Button, Card, Colors, Image, LoaderScreen, Text, TextField, View } from "react-native-ui-lib";
import { image_forgetPassword, image_imageSendEmail, image_mail } from "../../image";
import { colors } from "../../assets/color/color";
import { FontFamily } from "../global";
import i18next from "i18next";
import { useTranslation, Trans } from 'react-i18next';


const ForgetPassword = ({ navigation }: any) => {
  const [email, onChangeEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [statusSendEmail, setStatusSendEmail] = useState(false)
  // const { i18n, t } = useTranslation();

  const handleSendEmail = () => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
      setStatusSendEmail(true)
    }, 2000);
  }

  return (
    <>
      {!statusSendEmail ? (
        <SafeAreaView style={{ flex: 1 }}>
          <View style={{ flex: 0.3 }} center>
            <Text style={{ color: colors.primary, fontSize: 24, fontWeight: "600", fontFamily: FontFamily.interMedium }}>{i18next.t('forgotPasswordScr.title')}</Text>
          </View>
          <View style={{ flex: 0.2 }} center>
            <Image style={{ width: 179, height: 179 }} source={image_forgetPassword} />
          </View>
          <View style={{ flex: 0.2 }} center>
            <Text style={{ color: colors.text, fontSize: 14, fontWeight: "400", fontFamily: FontFamily.interMedium, textAlign: 'center' }}>{i18next.t('forgotPasswordScr.subTitle')}</Text>
          </View>
          <View center style={{ position: "relative" }}>
            <TextField
              containerStyle={{ ...styles.containerTextField, marginBottom: 24 }}
              placeholder={i18next.t('forgotPasswordScr.acc')}
              floatingPlaceholder
              onChangeText={(value) => onChangeEmail(value)}
              enableErrors
              validate={['required', 'email', (value: any) => value.length > 6]}
              validationMessage={['Field is required', 'Email is invalid', 'Password is too short']}
              maxLength={30}
            />
            <View style={{ position: "absolute", left: '10%', top: '18%' }}>
              <Image style={{ width: 30, height: 30 }} source={image_mail} />
            </View>
          </View>
          <View style={{ flex: 0.1, paddingHorizontal: Dimensions.get("screen").width * 0.08 }}>
            <Button onPress={() => handleSendEmail()} borderRadius={8} labelStyle={{ fontSize: 14, fontWeight: "500", fontFamily: FontFamily.interMedium }} label={i18next.t('forgotPasswordScr.sendRequest')} backgroundColor={colors.primary} />
          </View>
        </SafeAreaView>
      ) : (
        <SafeAreaView style={{ flex: 1 }}>
          <View style={{ flex: 2.4 }} center>
            <Image style={{ width: 342, height: 368 }} source={image_imageSendEmail} />
          </View>
          <View style={{ flex: 0.5 }} center>
            <Text style={{ color: colors.text, fontSize: 14, fontWeight: "400", fontFamily: FontFamily.interMedium }}>{i18next.t('forgotPasswordScr.requestSuccess')}</Text>
          </View>
          <View style={{ flex: 0.8, paddingHorizontal: Dimensions.get("screen").width * 0.08 }}>
            <Button onPress={() => navigation.reset({
              routes: [
                { name: 'SignIn' },
              ],
            })} borderRadius={8} labelStyle={{ fontSize: 14, fontWeight: "500", fontFamily: FontFamily.interMedium }} label={i18next.t('forgotPasswordScr.login')} backgroundColor={colors.primary} />
          </View>
        </SafeAreaView>
      )}
      {loading && <LoaderScreen backgroundColor="rgba(0, 53, 128, 0.15)" overlay message={''} color={colors.primary} />}
    </>
  )
}

const styles = StyleSheet.create({
  containerTextField: {
    borderWidth: 1,
    borderStyle: "solid",
    borderRadius: 8,
    width: "85%",
    paddingHorizontal: Platform.OS === 'ios' ? 20 : 14,
    borderColor: 'rgba(0, 53, 128, 0.15)',
    marginBottom: 16,
    paddingLeft: 50
  },
})

export default ForgetPassword;