import React from "react";
import { SafeAreaView } from "react-native";
import { Toast } from "react-native-ui-lib";

interface IToast {
  open: boolean,
  onOpen: Function,
  message: string,
  position?: string,
}

const ToastComponent = (props: IToast) => {
  const { position = 'top' } = props
  return (
    <Toast
      visible={props.open}
      position={position}
      autoDismiss={2000}
      message={props.message}
      onDismiss={() => props.onOpen(false)}
    ></Toast>
  )
}

export default ToastComponent;